body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.central {
    position: relative;
    left: 50%;
    transform: translate(-50%, 0);
}

.vertical {
    position: relative;
    top: 50%;
    transform: translate(0%, -50%);
}

nav {
    height: 50px;
    border-bottom: solid black 3px;
}

.nav-item {
    text-decoration: none;
    color: coral;
    font-size: 18px;
}

#nav-user {
    float: left;
    margin-left: 20px;
}

#nav-about {
    float: right;
    margin-right: 20px;
}

#nav-auc {
    margin-right: 10px;
}
#nav-art {
    margin-left: 10px;
}

.gallery-container {
    margin-top: 50px;
    width: 825px;
    height: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr ;
    column-gap: 5px;
    row-gap: 0;
}

.artwork-lite {
    margin-bottom: 20px;
    width: 250px;
}

.artwork-image {
    width: 100%;
}
.artwork-image-lite {
    width: 300px;
}

.auction-lite-container {
    margin-top: 30px;
}

.auction-lite {
    border: solid black 1px;
}

.auction-component {
    height: 250px;
    text-align: left;
    margin-left: 12px;
    display: inline-block;
}
.art-component {
    display: inline-block;

}
.end-text{
    margin :0;
    padding-top : 5px;
}
.end-date {
    margin :0;
    padding-top : 5px;
    padding-bottom: 30px;
}
.bid-now {
    vertical-align: bottom;
    padding: 6px;
    font-size: 22px;
    text-decoration: none;
    color: black;
    border: solid black 2px;
}